    export default {
        name: 'app-head-navigation',
	    props: {
            list: {
                type: Array,
	            default: function() {
	                return [];
	            }
            },
			theme: Object,
	    },
	    methods: {
            active(item) {
                this.isSwitch = false;
                console.log(item);
                this.$emit('click', item);
            }
	    },
        data() {
            return {
                activeIndex: 0,
                isSwitch: false,
                scrollLeft: 0
            }
        },
        watch: {
            list: {
                handler(newVal) {
                    for (let i = 0; i < newVal.length; i++) {
                        if (newVal[i].active && i > 1) {
                            this.activeIndex = i - 2;
                        } else if (newVal[i].active && i <= 1) {
                            this.activeIndex = 0;
                        }
                    }
                },
                immediate: true,
                deep: true
            }
        }
    }
